body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.list {
  width: 500px;
  max-width: 100%;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

/* style h1 from app.js */
h1 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0 0 1rem 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 400;
}

/* SMILE EMOTE */
 .gg-list {
 box-sizing: border-box;
 position: relative;
 display: inline-block;
 transform: scale(var(--ggs,1));
 width: 22px;
 height: 20px;
 border: 2px solid;
 border-radius: 3px
}

.gg-list::after,
.gg-list::before {
 content: "";
 display: block;
 box-sizing: border-box;
 position: absolute;
 width: 2px;
 height: 2px;
 background: currentColor;
 top: 3px;
 left: 3px;
 box-shadow:
 0 4px 0,
 0 8px 0
}

.gg-list::after {
 border-radius: 3px;
 width: 8px;
 left: 7px
} 
/* SMILE EMOTE FINISH */